export function trackShioEvent(type: string, status: boolean, tx: string) {
  if (typeof window !== 'undefined' && (window as any).gtag) {
    ;(window as any).gtag('event', 'track_shio_status', {
      operation_type: type,
      shio_enabled: status,
      tx_hash: tx
    })
  }
}

type CommonTrackData = {
  action: string
  txHash: string
  actionTime: number
}

type SwapTrackData = CommonTrackData & {
  isOpenAggregator: boolean // ÊòØÂê¶ÂºÄÂêØAggregator
  isDowngrade: boolean // ÊòØÂê¶ÈôçÁ∫ß
  providers: string[] // ÂºÄÂêØÁöÑË∑ØÂæÑ
  // errorMsg: string
  fromCoin?: string // Áî®-ÂàÜÂâ≤ÔºåÂêéÈù¢Âä†Êï∞Èáè
  toCoin?: string // Áî®-ÂàÜÂâ≤, ÂêéÈù¢Âä†Êï∞Èáè
  // acountIn?: string
  // amountOut?: string
  isError: boolean
  errorMsg1?: string
  errorMsg2?: string
  errorMsg3?: string
  errorMsg4?: string
  errorMsg5?: string
  useMev?: boolean
  useFastMode?: boolean
}

export function trackSwap(data: SwapTrackData) {
  console.log('üöÄ ~ file: gtag.ts:30 ~ trackTransaction ~ data:', data)
  const providerArr = data.providers.map((item: string) => TrackDataProviders?.[item])
  const providerText = providerArr.join('')

  console.log('üöÄ ~ file: gtsag.ts:30 ~ trackTransaction ~ final data:', {
    ...data,
    providers: providerText
  })

  if (typeof window !== 'undefined' && (window as any).gtag) {
    ;(window as any).gtag('event', 'track_transaction', {
      ...data,
      providers: providerText
    })
  }
}

export function trackTransactionError(data: any) {
  if (typeof window !== 'undefined' && (window as any).gtag) {
    ;(window as any).gtag('event', 'track_transaction_error', {
      ...data
    })
  }
}

export function trackWalletConnect({ walletName, walletAddress }: { walletName: string; walletAddress: string }) {
  if (typeof window !== 'undefined' && (window as any).gtag) {
    ;(window as any).gtag('event', 'wallet_connection', {
      walletName,
      walletAddress
    })
  }
}

export function trackDlmmBetaWalletConnect({
  walletName,
  walletAddress,
  inviteCodes,
  connectTime
}: {
  walletName: string
  walletAddress: string
  inviteCodes: string
  connectTime: number
}) {
  if (typeof window !== 'undefined' && (window as any).gtag) {
    try {
      console.log('trackDlmmBetaWalletConnect: ', {
        walletName,
        walletAddress: String(walletAddress)?.split('0x')[1],
        inviteCodes,
        connectTime
      })
      ;(window as any).gtag('event', 'dlmm_beta_wallet_connection', {
        walletName,
        walletAddress: String(walletAddress)?.split('0x')?.[1],
        inviteCodes,
        connectTime
      })
    } catch (error) {
      console.log('üöÄ ~ trackDlmmBetaWalletConnect ~ error:', error)
    }
  }
}

