import envConfigs from '@cetus/types/src/config/envConfigs'
import { trackDlmmBetaWalletConnect, trackDlmmTransaction, trackShioEvent, trackSwap, trackTransactionError, trackWalletConnect } from '@cetus/utils'
import { fixCoinType } from '@cetusprotocol/common-sdk'
import { v4 } from 'uuid'

export default function useTrack() {
  const trackShio = (data: any, status: boolean, txHash: string) => {
    const type = data?.action
    const supported = [
      'FarmingOpenAndAddLiquidity',
      'FarmingIncreaseLiquidity',
      'FarmingDecreaseLiquidity',
      'FarmingRemoveLiquidity',
      'FarmingRemoveLiquidity',
      'OpenAndAddLiquidity',
      'IncreaseLiquidity',
      'RemoveLiquidity',
      'DecreaseLiquidity',
      'AddVaultsPosition',
      'RemoveVaultsPosition',
      'AggregatorSwap'
    ]

    if (supported.includes(type)) {
      try {
        trackShioEvent(type, status, txHash)
      } catch (e) {
        console.log('üöÄ ~ useTransaction ~ trackShioEvent error:', e)
      }
    }
  }

  const toTrackSwap = (params: any, error: string, txHash: string) => {
    try {
      const action = 'Swap'
      const actionTime = new Date().getTime()

      const isOpenAggregator = params?.providerKey?.length > 1
      const isDowngrade = !!params?.routerData?.isDowngrade
      const providers = params?.providerKey || []
      const fromCoin = `${params?.from}-${params?.routerData?.fromAmountUi}`
      const toCoin = `${params?.to}-${params?.routerData?.toAmountUi}`
      const isError = !!error

      const data: any = {
        action,
        actionTime,
        txHash,
        isOpenAggregator,
        isDowngrade,
        providers,
        fromCoin,
        toCoin,
        isError: !!error,
        useMev: params?.useMev,
        useFastMode: params?.useFastMode
      }
      console.log('üöÄ ~ trackSwap ~ data:', data)

      if (isError) {
        const errorString = String(data?.error)
          .replace(/\\/g, '') // ÂéªÊéâÊâÄÊúâÂèçÊñúÊù†
          .replace(/"/g, '') // ÂéªÊéâÊâÄÊúâÂèåÂºïÂè∑
          .replace(/\s+/g, ' ') // Â∞ÜÂ§ö‰∏™ËøûÁª≠Á©∫ÁôΩÂ≠óÁ¨¶ÊõøÊç¢‰∏∫Âçï‰∏™Á©∫Ê†º
          .trim() // ÂéªÊéâÈ¶ñÂ∞æÁ©∫Ê†º

        // ËÆ∞ÂΩïÈïøÂ∫¶
        data['errorStringLength'] = errorString.length

        // ‰ΩøÁî®ÈÄöÁî®ÊñπÊ≥ïÊãÜÂàÜÂ≠òÂÇ®
        splitStringToEventData(data, errorString, 'errorMsg', 25, 100)
      }

      console.log('useTrack üöÄ ~ trackSwap ~ data:', data)
      trackSwap(data)
    } catch (error) {
      console.log('üöÄ ~ useTrack ~ error:', error)
    }
  }

  const toTrackWalletConnect = ({ walletAddress, walletName }: { walletAddress: string; walletName: string }) => {
    // ‰∏ÄÂ§©‰∏Ä‰∏™Âú∞ÂùÄÂØπÂ∫î‰∏Ä‰∏™Èí±ÂåÖÂè™‰∏äÊä•‰∏ÄÊ¨°
    const today = new Date().toISOString().split('T')[0] // Ëé∑ÂèñÂΩìÂ§©Êó•Êúü (YYYY-MM-DD)
    const key = `wallet_connected_${walletName}_${walletAddress}`

    const lastConnectionDate = localStorage.getItem(key)

    if (!lastConnectionDate || lastConnectionDate !== today) {
      localStorage.setItem(key, today) // Êõ¥Êñ∞‰∏∫‰ªäÂ§©ÁöÑÊó•Êúü
      trackWalletConnect({
        walletAddress,
        walletName
      })
    }
  }

  // ÈÄöÁî®ÊñπÊ≥ïÔºöÂ∞ÜÂ≠óÁ¨¶‰∏≤ÊåâÊåáÂÆöÈïøÂ∫¶ÊãÜÂàÜÂπ∂Â≠òÂÇ®Âà∞eventData‰∏≠
  const splitStringToEventData = (eventData: any, sourceString: string, keyPrefix: string, maxKeys: number = 25, chunkSize: number = 100) => {
    if (!sourceString || sourceString.length === 0) {
      return eventData
    }

    // ËÆ°ÁÆóÂ∑≤Â≠òÂú®ÁöÑkeyÊï∞Èáè
    const existingKeys = Object.keys(eventData).length

    // ËÆ°ÁÆóËøòËÉΩÂ≠òÂÇ®ÁöÑkeyÊï∞Èáè
    const remainingKeys = maxKeys - existingKeys

    // ËÆ°ÁÆóÈúÄË¶ÅÁöÑkeyÊï∞Èáè
    const neededKeys = Math.ceil(sourceString.length / chunkSize)

    if (neededKeys <= remainingKeys) {
      // Â¶ÇÊûúÂâ©‰ΩôÁ©∫Èó¥Ë∂≥Â§üÔºåÂ≠òÂÇ®ÊâÄÊúâÂÜÖÂÆπ
      let i = 0
      while (i * chunkSize < sourceString.length) {
        const startIndex = i * chunkSize
        const endIndex = Math.min(startIndex + chunkSize, sourceString.length)
        eventData[`${keyPrefix}${i}`] = sourceString.slice(startIndex, endIndex)
        i++
      }
    } else {
      // Â¶ÇÊûúÂâ©‰ΩôÁ©∫Èó¥‰∏çË∂≥ÔºåÂ≠òÂÇ®ÂâçÂçäÈÉ®ÂàÜÂíåÂêéÂçäÈÉ®ÂàÜ
      const halfKeys = Math.floor(remainingKeys / 2)

      // Â≠òÂÇ®ÂâçÂçäÈÉ®ÂàÜ
      for (let i = 0; i < halfKeys; i++) {
        const startIndex = i * chunkSize
        const endIndex = Math.min(startIndex + chunkSize, sourceString.length)
        eventData[`${keyPrefix}${i}`] = sourceString.slice(startIndex, endIndex)
      }

      // Â≠òÂÇ®ÂêéÂçäÈÉ®ÂàÜ
      const startFromEnd = neededKeys - (remainingKeys - halfKeys)
      for (let i = 0; i < remainingKeys - halfKeys; i++) {
        const actualIndex = startFromEnd + i
        const startIndex = actualIndex * chunkSize
        const endIndex = Math.min(startIndex + chunkSize, sourceString.length)
        eventData[`${keyPrefix}${halfKeys + i}`] = sourceString.slice(startIndex, endIndex)
      }
    }

    return eventData
  }

  const toTrackTransactionError = (data: any) => {
    console.log('üöÄ ~ trackTransactionError ~ data:', data)
    const eventData: any = {}

    eventData['timestamp'] = new Date().getTime()

    // Â§ÑÁêÜ paramsJson
    if (data?.paramsJson) {
      const paramsJson = String(data?.paramsJson)
        .replace(/\\/g, '') // ÂéªÊéâÊâÄÊúâÂèçÊñúÊù†
        .replace(/"/g, '') // ÂéªÊéâÊâÄÊúâÂèåÂºïÂè∑
        .replace(/\s+/g, ' ') // Â∞ÜÂ§ö‰∏™ËøûÁª≠Á©∫ÁôΩÂ≠óÁ¨¶ÊõøÊç¢‰∏∫Âçï‰∏™Á©∫Ê†º
        .trim() // ÂéªÊéâÈ¶ñÂ∞æÁ©∫Ê†º

      console.log('üöÄ ~ trackTransactionError ~ paramsJson:', paramsJson)

      // ËÆ∞ÂΩïÈïøÂ∫¶
      eventData['paramsJsonLength'] = paramsJson.length

      // ‰ΩøÁî®ÈÄöÁî®ÊñπÊ≥ïÊãÜÂàÜÂ≠òÂÇ®
      splitStringToEventData(eventData, paramsJson, 'paramsJson', 25, 100)
    }

    // Â§ÑÁêÜ error ‰ø°ÊÅØ
    if (data?.error) {
      const errorString = String(data?.error)
        .replace(/\\/g, '') // ÂéªÊéâÊâÄÊúâÂèçÊñúÊù†
        .replace(/"/g, '') // ÂéªÊéâÊâÄÊúâÂèåÂºïÂè∑
        .replace(/\s+/g, ' ') // Â∞ÜÂ§ö‰∏™ËøûÁª≠Á©∫ÁôΩÂ≠óÁ¨¶ÊõøÊç¢‰∏∫Âçï‰∏™Á©∫Ê†º
        .trim() // ÂéªÊéâÈ¶ñÂ∞æÁ©∫Ê†º

      console.log('üöÄ ~ trackTransactionError ~ errorString:', errorString)

      // ËÆ∞ÂΩïÈïøÂ∫¶
      eventData['errorStringLength'] = errorString.length

      // ‰ΩøÁî®ÈÄöÁî®ÊñπÊ≥ïÊãÜÂàÜÂ≠òÂÇ®
      splitStringToEventData(eventData, errorString, 'errorMsg', 25, 100)
    }

    console.log('üöÄ ~ trackTransactionError ~ eventData:', eventData)
    trackTransactionError(eventData)
  }

  const toTrackDlmmBetaWalletConnect = (data: { walletName: string; walletAddress: string; inviteCodes: string }) => {
    trackDlmmBetaWalletConnect({
      walletName: data?.walletName,
      walletAddress: String(data?.walletAddress),
      inviteCodes: data?.inviteCodes,
      connectTime: new Date().getTime()
    })
  }

  const toTrackDlmmTransaction = (data: {
    walletName: string
    walletAddress: string
    params: any
    action: string
    txHash?: string
    isError?: boolean
    error?: any
  }) => {
    // testnet‰∏çÂüãÁÇπ
    if (envConfigs.env === 'testnet') {
      return
    }
    const eventData: any = {
      trackId: v4(),
      action: data?.action,
      timestamp: new Date().getTime(),
      walletName: data?.walletName,
      walletAddress: fixCoinType(data?.walletAddress),

      isError: data?.isError
    }

    if (data?.txHash) {
      eventData['txHash'] = data?.txHash
    }

    // Â§ÑÁêÜ paramsJson
    if (data?.params) {
      const paramsJson = JSON.stringify(data?.params, null, 0)
        .replace(/\\/g, '') // ÂéªÊéâÊâÄÊúâÂèçÊñúÊù†
        .replace(/"/g, '') // ÂéªÊéâÊâÄÊúâÂèåÂºïÂè∑
        .replace(/\s+/g, ' ') // Â∞ÜÂ§ö‰∏™ËøûÁª≠Á©∫ÁôΩÂ≠óÁ¨¶ÊõøÊç¢‰∏∫Âçï‰∏™Á©∫Ê†º
        .trim() // ÂéªÊéâÈ¶ñÂ∞æÁ©∫Ê†º // ËÆ∞ÂΩïÈïøÂ∫¶

      if (paramsJson.length > 1000) {
        eventData['paramsJsonLength'] = paramsJson.length
      }

      // ‰ΩøÁî®ÈÄöÁî®ÊñπÊ≥ïÊãÜÂàÜÂ≠òÂÇ®
      splitStringToEventData(eventData, paramsJson, 'paramsJson', 25, 100)
    }

    // Â§ÑÁêÜ error ‰ø°ÊÅØ
    if (data?.error) {
      const errorString = String(data?.error)
        .replace(/\\/g, '') // ÂéªÊéâÊâÄÊúâÂèçÊñúÊù†
        .replace(/"/g, '') // ÂéªÊéâÊâÄÊúâÂèåÂºïÂè∑
        .replace(/\s+/g, ' ') // Â∞ÜÂ§ö‰∏™ËøûÁª≠Á©∫ÁôΩÂ≠óÁ¨¶ÊõøÊç¢‰∏∫Âçï‰∏™Á©∫Ê†º
        .trim() // ÂéªÊéâÈ¶ñÂ∞æÁ©∫Ê†º

      // ËÆ∞ÂΩïÈïøÂ∫¶

      eventData['errorStringLength'] = errorString.length

      // ‰ΩøÁî®ÈÄöÁî®ÊñπÊ≥ïÊãÜÂàÜÂ≠òÂÇ®
      splitStringToEventData(eventData, errorString, 'errorMsg', 25, 100)
    }

    console.log('üöÄ ~ toTrackDlmmTransaction ~ eventData:', eventData)
    trackDlmmTransaction({ ...eventData })
  }

  return {
    trackShio,
    toTrackSwap,
    toTrackWalletConnect,
    toTrackTransactionError,
    toTrackDlmmBetaWalletConnect,
    toTrackDlmmTransaction
  }
}
